@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Roboto+Slab&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Lato', sans-serif;
  height: 100vh;
  background: #232333;
  background: radial-gradient(ellipse at center, #232333 0%, #161616 100%);
  /* overflow: hidden; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo_holder {
  color: white;
  position: absolute;
  width: 380px;
  text-align: center;
  top: 15%;
  left: 9%;
  -webkit-animation: move_up 0.5s 1.9s forwards;
  animation: move_up 0.5s 1.9s forwards;
}
.left-text {
  flex: 1 1;
}
.right-picture {
  margin-right: 5%;
  flex: 1 1;
}
h4 {
  margin: 0;
}
.main-split {
  display: flex;
  flex: 2 1;
height: 100vh;

}
.logo_holder__rotate {
  -webkit-animation: logo_rotate 0.35s 0.4s forwards,
    logo_move 0.35s 0.75s forwards;
  animation: logo_rotate 0.35s 0.4s forwards, logo_move 0.35s 0.75s forwards;
  transform: translateY(-50%) rotate(0deg) scale(0.5);
  position: relative;
  margin-top: 15px;
}
.logo_holder__right {
  transform: rotate(62.1deg);
  -webkit-animation: logo_right 0.35s forwards;
  animation: logo_right 0.35s forwards;
  left: 2000px;
  position: relative;
  top: 11px;
}
.logo_holder__left {
  transform: rotate(-118deg);
  -webkit-animation: logo_left 0.35s forwards;
  animation: logo_left 0.35s forwards;
  left: 2000px;
  position: relative;
}
.logo_holder__text {
  transform: scale(0.5);
  position: absolute;
  left: 15%;
  font-size: 5em;
  text-align: end;
  -webkit-animation: logo_text 0.4s 1.1s forwards;
  animation: logo_text 0.4s 1.1s forwards;
  margin: 0;
  opacity: 0;
  font-family: 'Press Start 2P', cursive;
  line-height: normal;
}

@-webkit-keyframes logo_right {
  0% {
    left: 2000px;
  }
  100% {
    left: 68px;
  }
}

@keyframes logo_right {
  0% {
    left: 2000px;
  }
  100% {
    left: 68px;
  }
}
@-webkit-keyframes logo_left {
  0% {
    left: -2000px;
  }
  100% {
    left: -68px;
  }
}
@keyframes logo_left {
  0% {
    left: -2000px;
  }
  100% {
    left: -68px;
  }
}
@-webkit-keyframes logo_rotate {
  0% {
    transform: translateY(-50%) rotate(0deg) scale(0.5);
  }
  100% {
    transform: translateY(-50%) rotate(-60deg) scale(0.5);
  }
}
@keyframes logo_rotate {
  0% {
    transform: translateY(-50%) rotate(0deg) scale(0.5);
  }
  100% {
    transform: translateY(-50%) rotate(-60deg) scale(0.5);
  }
}
@-webkit-keyframes logo_move {
  0% {
    left: 0;
  }
  100% {
    left: -90px;
  }
}
@keyframes logo_move {
  0% {
    left: 0;
  }
  100% {
    left: -90px;
  }
}
@-webkit-keyframes logo_text {
  0% {
    top: -30px;
    opacity: 0;
  }
  100% {
    top: -64px;
    opacity: 1;
  }
}
@keyframes logo_text {
  0% {
    top: -30px;
    opacity: 0;
  }
  100% {
    top: -64px;
    opacity: 1;
  }
}
.profile-pic {
  width: 63%;
    height: 570px;
  background-size: (200px 300px);
  background-repeat: no-repeat;
  background-image: url(/websitesd/static/media/pic.89073169.png);
}

.width-550px {
  width: 450px;
}

.swiper-bottom-scrollbar-full .swiper-slide .profile-pic {
  max-height: 800px;
  opacity: 0.8;
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
  -webkit-filter: grayscale(1);
  -moz-filter: grayscale(1);
  -ms-filter: grayscale(1);
  -o-filter: grayscale(1);
}

.swiper-bottom-scrollbar-full .swiper-slide .profile-pic:hover   {
  opacity: 1;
  -webkit-filter: grayscale(0);
  -moz-filter: grayscale(0);
  -ms-filter: grayscale(0);
  -o-filter: grayscale(0);
  filter: grayscale(0);
  filter: contrast(130%);
  -webkit-filter: contrast(130%);
  -moz-filter: contrast(130%);
  -ms-filter: contrast(130%);
  -o-filter: contrast(130%);
  max-height: 800px;
}


